/*Обнуление*/
* {
   padding: 0;
   margin: 0;
   border: 0;
}
*,
*:before,
*:after {
   -moz-box-sizing: border-box;
   -webkit-box-sizing: border-box;
   box-sizing: border-box;
}
:focus,
:active {
   outline: none;
}
a:focus,
a:active {
   outline: none;
}
nav,
footer,
header,
aside {
   display: block;
}
html,
body {
   height: 100%;
   width: 100%;
   font-size: 100%;
   line-height: 1;
   font-size: 14px;
   -ms-text-size-adjust: 100%;
   -moz-text-size-adjust: 100%;
   -webkit-text-size-adjust: 100%;
}
input,
button,
textarea {
   font-family: inherit;
}
input::-ms-clear {
   display: none;
}
button {
   cursor: pointer;
}
button::-moz-focus-inner {
   padding: 0;
   border: 0;
}
a,
a:visited {
   text-decoration: none;
}
a:hover {
   text-decoration: none;
}
ul li {
   list-style: none;
}
img {
   vertical-align: top;
}
h1,
h2,
h3,
h4,
h5,
h6 {
   font-size: inherit;
   font-weight: 400;
}
/*--------------------*/
.App {
   overflow-x: hidden;
   display: grid;
   grid-template-rows: 60px 1fr 50px;
   grid-template-columns: 200px 1fr;
   gap: 10px;
   grid-template-areas:
      "h h"
      "s c"
      "f f";
   max-width: 100%;
   margin: 0 auto;
   @media (max-width: 900px) {
      grid-template-columns: 150px 1fr;
   }
}
.app__wrapper_content {
   grid-area: c;
   width: 100%;
   padding-right: 15px;
}

.active {
   position: relative;
   &::after {
      content: "";
      position: absolute;
      bottom: -3px;
      left: 0;
      width: 100%;
      height: 2px;
      background-color: yellow;
   }
}

.AppNotAuth {
   width: 100%;
   min-height: 100vh;
   overflow-x: hidden;
   display: flex;
   flex-direction: column;
}

.AppNotAuthLogin {
   flex: 1 1 auto;
}

body.App.lock {
   overflow: hidden;
}
